header {
  height: 100vh; }
  header .container {
    height: 100vh;
    display: flex;
    align-items: center;
    z-index: 4;
    position: relative;
    padding: 40px 80px;
    max-width: 75vw; }
    header .container .heading {
      color: white;
      margin: 0;
