      display: inline-block;
      padding-bottom: 40px; }
      header .container .heading .subheading {
        border-top: 1px solid rgba(255, 255, 255, 0.65);
        line-height: 1;
        display: inline-block;
        padding-top: 40px; }
  header .overlay {
    background-image: linear-gradient(45deg, #253a57, #D94700);
